import React from 'react';
import { createUseStyles } from 'react-jss'
import { GameCarousel } from '../../Components/GameCarousel';

const useStyles = createUseStyles({
    title: {
        color: 'white',
        textAlign: 'center',
    },
    body: {
        margin: '20px',
        fontSize: '24px',
        lineSize: '0px',
        textAlign: 'center',
    },
    text: {
        margin: '20px',
    },
    link: {
        color: 'cyan'
    },
});

export const HomePage = ({children}) => {
    const classes = useStyles();
    return (
        <div className={classes.body}>
            <h3 className={classes.text}>Hello there!</h3>
            <p className={classes.text}>Welcome to the personal website of Josh Stephens. On this site, you can <a className={classes.link} href="#/portfolio">check out some of my past projects</a>, <a className={classes.link} href="#/about">learn more about me</a> and <a className={classes.link} href="#/contact">how to contact me</a>, or <a className={classes.link} href="/content/Joshua-Stephens-Resume.pdf" rel="noopener noreferrer" target="_blank">check out my résumé.</a></p>

            <h2 className={classes.title}>Project Preview</h2>
            <GameCarousel/>
        </div>
    );
}
