import React from 'react';
import {createUseStyles} from 'react-jss'
import Slider from "react-slick";
import projects from './games.json';

const useStyles = createUseStyles({
    container: {
        color: 'white',
        background: 'radial-gradient(ellipse at center, #1e579988 0%,#256ec688 0%,#06307a88 100%)',
        borderRadius: 5,
        padding: 40,
        paddingTop: 0,
        fontSize: 30,
        border: '5px solid white',
        margin: '0px',
    },
    slideBody: {
        height: 'auto',
        textAlign: 'center',
    },
    title: {
        fontWeight: 'bold',
        textDecoration: 'none',
        fontFamily: 'Russo One',
    },
    link: {
        color: 'white',
        textDecoration: 'none',
        '&:hover': {
            color: 'cyan !important',
        }
    },
    slideImage: {
        maxWidth: '90%',
        maxHeight: '540px',
        display: 'inherit !important',
        margin: 'auto',
        background: 'transparent url(/content/images/loading.gif) 50% no-repeat'
    },
    description: {
        color: 'white !important',
        fontSize: '20px',
        paddingTop: 0,
        margin: '10px',
        fontWeight: 'bold',
    },
});

export const GameCarousel = ({children, url}) => {
    const classes = useStyles();
    var settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        draggable: false,
        fade: false,
        autoplay: true,
        autoplaySpeed: 10000,
    };

    return (
        <div className={classes.container}>
            <Slider {...settings}>
                {projects.map(p => (p.previewImage && p.name) ? (
                    <a href={p.url} className={classes.link}>
                        <div className={classes.slideBody}>
                            <p className={classes.title}>{p.name}</p>
                            <video loop autoPlay muted className={classes.slideImage}>
                                <source src={p.previewImage} type="video/mp4"/>
                            </video>
                        </div>
                        <p className={classes.description}>{p.shortText}</p>
                    </a>
                ) : null)}
            </Slider>
        </div>
    );
}
